.fieldset {
  margin: 0;
  padding: 0;
  border: 0;
}

.form-item, .bx--form-item {
  margin-bottom: 1.5rem;
  display: flex;
  flex-direction: column;
  flex: 1;
  align-items: flex-start;
}

.label, .bx--label {
  font-weight: 500;
  color: #515761;
  display: inline-block;
  vertical-align: baseline;
  margin-bottom: 14px;
}

@import "components/forms/checkbox";
@import "components/forms/textfield";
@import "components/forms/select";
@import "components/forms/radiobutton";
@import "components/forms/search";
