.dropdown ul {
	position: absolute;
	top: 2.5em;
	left: -9999px;
	opacity: 0;

	-webkit-transition: 0.1s linear opacity;

	min-width: 150px;
	background: #333;
	background: #340449;
	background: rgba(0,0,0,0.9);

	-webkit-box-shadow: 0 3px 3px rgba(0,0,0,0.5);
	-moz-box-shadow: 0 3px 3px rgba(0,0,0,0.5);
	box-shadow: 0 3px 3px rgba(0,0,0,0.5);
}

.dropdown li {
	float: left;
	position: relative;
  list-style: none;
}


.dropdown li > a {
	float: left;
	text-decoration: none;
	font-size: 16px;
  display: block;
  width: 100%;
  padding: 16px;
  color: #152935;
  cursor: pointer;
  background: #FFFFFF;
  border: 1px solid #515761;
  border-radius: 3px;
  transition: all 0.2s;
}


.dropdown li > a:hover,
.dropdown li > a:focus,
.dropdown li:focus > a,
.dropdown li:hover > a {
	background: #999;
	outline: 0;
}


.dropdown li:hover ul,
.dropdown li:focus ul,
ul.show-menu {
	left: 0;
	opacity: 0.99;
}

.dropdown ul li {
	float: none;
	position: static;
  list-style: none;
}

.dropdown ul a {
	float: none;
	display: block;
	color: #FFF;
	font-size: 12px;
	text-shadow: none;
	border: 1px solid #222;
	border-width: 0 0 1px;

	-webkit-transition: 0.1s linear all;
}


.dropdown ul a:hover,
.dropdown ul a:focus {
	background: #EEE;
	color: #333;
	text-shadow: none;
}

.dropdown__arrow {
  fill: #fff;
  right: 16px;
  width: 0.625rem;
  height: 0.3125rem;
	margin-left: 8px;
	margin-bottom: 4px;
  pointer-events: none;
}
