.accordion {
  list-style: none;
  margin: 0;
  padding: 0; }

.accordion__item {
  transition: all 0.1s cubic-bezier(0.2, 0.2, 0.38, 0.9);
  border-top: 1px solid #DCDDDF;
  overflow: visible; }

.accordion__item:last-child {
  border-bottom: 1px solid #dcdcdc; }

.accordion__heading {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: inherit;
  letter-spacing: 0;
  display: inline-block;
  background: none;
  appearance: none;
  border: 0;
  color: #171717;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  cursor: pointer;
  padding: 15px 0;
  position: relative;
  width: 100%;
  margin: 0;
  transition: background-color .1s; }

.accordion__heading:focus,
.accordion__item.focus .accordion__heading {
  outline: 2px solid #14568D;
  outline-offset: -2px; }

.accordion__arrow {
  outline-offset: -2px;
  flex: 0 0 1rem;
  width: 1rem;
  height: 1rem;
  margin: 0 1rem 0 0;
  fill: #171717; }

.accordion__title {
  font-size: 16px;
  color: #14568D;
  width: 100%;
  text-align: left;
  font-weight: 500; }

.accordion .plus {
  margin: 0 8px; }

.switch {
  position: relative;
  display: inline-block;
  width: 70px;
  height: 34px; }

.switch input {
  opacity: 0;
  width: 0;
  height: 0; }

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #F8F8F9;
  -webkit-transition: .4s;
  transition: .4s;
  border-radius: 34px;
  border: 1px solid #14568D; }

.slider:before {
  position: absolute;
  content: "";
  height: 28px;
  width: 28px;
  left: 1px;
  bottom: 1px;
  background-color: white;
  -webkit-transition: .4s;
  transition: .4s;
  border-radius: 50%;
  border: 1px solid #515761; }

input:checked + .slider {
  background-color: rgba(20, 86, 141, 0.7); }

input:focus + .slider {
  box-shadow: 0px 0px 0px 2px white, 0px 0px 0px 4px #14568D; }

input:checked + .slider:before {
  transform: translateX(36px); }

