.radio-button {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0,0,0,0);
  border: 0;
  visibility: visible;
  white-space: nowrap;
  visibility: unset;
}

.radio-button__label {
  font-size: 16px;
  display: flex;
  align-items: center;
  cursor: pointer;
  margin: 0 8px 8px 0;
  color: #515761;
}

.radio-button:checked + .radio-button__label .radio-button__appearance {
  display: flex;
  align-items: center;
  justify-content: center;
}

.radio-button__appearance {
  background-color: #fff;
  border-radius: 50%;
  flex-shrink: 0;
  height: 24px;
  width: 24px;
  margin-right: 8px;
  background: #fff;
  border: 1px solid #515761;
  box-shadow: 0 0 5px 0 rgba(0,0,0,0.10);
}

.radio-button__label:hover {
  font-weight: 500;
}

.radio-button__label:hover .radio-button__appearance {
  background: #009BDF;
}

.radio-button:checked + .radio-button__label .radio-button__appearance {
  background: #009BDF;
}

.radio-button:checked + .radio-button__label .radio-button__appearance:before {
  content: "";
  display: inline-block;
  position: relative;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background: #FFFFFF;
  box-shadow: 0 0 5px 0 rgba(0,0,0,0.50);
}

.radio-button:focus + .radio-button__label .radio-button__appearance {
  box-shadow: 0px 0px 0px 2px white, 0px 0px 0px 4px #14568D;
}

.radio-button:disabled + .radio-button__label {
  cursor: not-allowed;
  font-weight: 400;
  opacity: 0.5;
}

.radio-button:disabled + .radio-button__label:hover .radio-button__appearance {
  background: transparent;
}

.radio-button:checked + .radio-button__label {
  font-weight: 500;
}
