.page {
  width: 1200px;
  min-height: 800px;
  background: white;
  box-shadow: 0px 0px 10px 4px rgba(0, 0, 0, 0.1);
  margin: 40px;

  .accredo-logo {
     margin: 16px 0 0 24px;
  }

  .container {
    width: 980px;
    margin: 0 auto;
  }

  .float-left {
    float: left;
  }

  .float-right {
    float: right;
  }

  .brand-bar {
    background-color: rgba(0, 124, 137, 1);
    color: #ffffff;
    height: 80px;
    width: 100%;
  }

  .navbar {
    background-color: rgba(21, 164, 167, 1);
    color: #ffffff;
    width: 100%;
    overflow: auto;
  }

  .nav {
    background: transparent;
    padding: 0;
    margin: 0 0 0 8px;
    width: auto;

    li {
      list-style: none;
      display: inline;

      &.active > a:before {
        border-bottom: 12px solid #FFF;
        border-left: 12px solid transparent;
        border-right: 12px solid transparent;
        bottom: 0;
        content: '';
        height: 0;
        left: 50%;
        margin-left: -12px;
        position: absolute;
        width: 0;
      }

      &.active > a:hover {
        background: none;
        box-shadow: none;
        color: inherit;
        cursor: default;
      }
    }
  }

  .nav li a {
    color: #FFF;
    font-weight: 400;
    font-size: 19px;
    line-height: 24px;
    margin: 0;
    padding: 16px;
    text-decoration: none;
    display: inline-block;
    position: relative;

    &:hover, &:focus {
      background-color: #FFF;
      box-shadow: 0px 2px 8px 0px rgba(84,87,89,0.32);
      color: #007c89;
    }

    &:focus {
      outline: 0;
    }
  }

  h1 {
    font-size: 40px;
    font-weight: 300;
    margin-left: 24px;
    color: #515761;
  }
  a.h2 {
    font-size: 24px;
    color: #007c89;
    text-decoration: underline;
    margin: 0;
    padding: 0;
    font-weight: 400;
  }
  h3 {
    font-size: 16px;
    font-weight: 500;
    color: #515761;
    text-transform: uppercase;
    margin-left: 24px;
  }
  p {
    font-size: 16px;
    font-weight: 300;
    line-height: 24px;
    color: #363f47;
    padding: 0;
    margin: 0;
  }

  .card {
    border-top: 1px solid #979aa0;
    border-bottom: 1px solid #979aa0;
    display: flex;
    flex-direction: row;
    margin: -1px 0 0 0;
    padding: 24px;

    &.light-green {
      background: #F3FAFA;
    }
    &.light-blue {
      background: #F2FAFD;
    }

    &.no-top-border {
      border-top: 0;
    }
  }
  .col-fixed-82 {
    width: 82px;
  }
  .col-1 {
    flex: 1;
  }
  .col-2 {
    display: flex;
    flex-direction: column;
    flex: 5;
  }
  .card-top {
    margin-bottom: 8px;
  }
  .card-body {
    display: flex;
    flex-direction: row;
    flex: 3;
    display: flex;
    flex-direction: row;
  }
}
