.checkbox {
  position: absolute;
  opacity: 0;

  & + label {
    position: relative;
    cursor: pointer;
    padding: 0;
    font-weight: 400;
    color: #515761;
  }

  & + label:before {
    content: '';
    margin: -3px 8px 11px 3px;
    display: inline-block;
    vertical-align: text-top;
    width: 24px;
    height: 24px;
    background: white;
    border: 1px solid #515761;
    border-radius: 4px;
  }

  &:hover + label, &:checked + label {
    font-weight: 500;
  }

  &:hover + label:before {
    background: #4786A2;
  }

  &:focus + label:before {
    box-shadow: 0px 0px 0px 2px white, 0px 0px 0px 4px #14568D !important;
  }

  &:checked + label:before {
    background: #4786A2;
  }

  &:checked + label:after {
    content: '';
    position: absolute;
    left: 8px;
    top: 4px;
    width: 15px;
    height: 12px;
    background-image: url("data:image/svg+xml,%3C%3Fxml version='1.0' encoding='UTF-8'%3F%3E%3Csvg width='15px' height='12px' viewBox='0 0 15 12' version='1.1' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink'%3E%3Cg stroke='none' stroke-width='1' fill='none' fill-rule='evenodd'%3E%3Cg transform='translate(-1.000000, -3.000000)' fill='%23FFFFFF' stroke='%23FFFFFF'%3E%3Cg%3E%3Cpath d='M6.01669299,12.2167153 L3.22216775,9.33577015 C2.94202132,9.04777503 2.48845489,9.04843549 2.20909875,9.33724535 C1.9297426,9.6260552 1.93038321,10.093648 2.21052958,10.3816432 L5.5108739,13.7840497 C5.79025478,14.0719834 6.2431312,14.0719834 6.52251207,13.7840497 L14.7894704,5.26143813 C15.0696168,4.97344295 15.0702574,4.50585008 14.7909013,4.2170402 C14.5115451,3.92823033 14.0579786,3.92756989 13.7778322,4.21556506 L6.01669299,12.2167153 Z' id='Shape'%3E%3C/path%3E%3C/g%3E%3C/g%3E%3C/g%3E%3C/svg%3E%0A");
  }

  &:disabled + label {
    color: #515761;
    cursor: not-allowed;
    font-weight: 400;
    opacity: 0.5;
  }

  &:disabled + label:before {
    box-shadow: none;
    background: #F8F8F9;
    border: 1px solid #B9BCC0;
  }

  &:disabled + label:after {
    background-image: url("data:image/svg+xml,%3C%3Fxml version='1.0' encoding='UTF-8'%3F%3E%3Csvg width='15px' height='12px' viewBox='0 0 15 12' version='1.1' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink'%3E%3Cg stroke='none' stroke-width='1' fill='none' fill-rule='evenodd'%3E%3Cg transform='translate(-1.000000, -3.000000)' fill='%23515761' stroke='%23515761'%3E%3Cg%3E%3Cpath d='M6.01669299,12.2167153 L3.22216775,9.33577015 C2.94202132,9.04777503 2.48845489,9.04843549 2.20909875,9.33724535 C1.9297426,9.6260552 1.93038321,10.093648 2.21052958,10.3816432 L5.5108739,13.7840497 C5.79025478,14.0719834 6.2431312,14.0719834 6.52251207,13.7840497 L14.7894704,5.26143813 C15.0696168,4.97344295 15.0702574,4.50585008 14.7909013,4.2170402 C14.5115451,3.92823033 14.0579786,3.92756989 13.7778322,4.21556506 L6.01669299,12.2167153 Z' id='Shape'%3E%3C/path%3E%3C/g%3E%3C/g%3E%3C/g%3E%3C/svg%3E%0A");
  }

  &[data-invalid] + label:before {
    background: #FFFFFF;
    border: 1px solid #D8171B;
    box-shadow: 0 0 5px 0 rgba(230,92,92,0.30);
    border-radius: 3px;
  }

  &[data-invalid] + label {
    color: #E4292F;
  }

  &[data-invalid] + label:after {
    background-image: url("data:image/svg+xml,%3C%3Fxml version='1.0' encoding='UTF-8'%3F%3E%3Csvg width='15px' height='12px' viewBox='0 0 15 12' version='1.1' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink'%3E%3Cg stroke='none' stroke-width='1' fill='none' fill-rule='evenodd'%3E%3Cg transform='translate(-1.000000, -3.000000)' fill='%23E4292F' stroke='%23E4292F'%3E%3Cg%3E%3Cpath d='M6.01669299,12.2167153 L3.22216775,9.33577015 C2.94202132,9.04777503 2.48845489,9.04843549 2.20909875,9.33724535 C1.9297426,9.6260552 1.93038321,10.093648 2.21052958,10.3816432 L5.5108739,13.7840497 C5.79025478,14.0719834 6.2431312,14.0719834 6.52251207,13.7840497 L14.7894704,5.26143813 C15.0696168,4.97344295 15.0702574,4.50585008 14.7909013,4.2170402 C14.5115451,3.92823033 14.0579786,3.92756989 13.7778322,4.21556506 L6.01669299,12.2167153 Z' id='Shape'%3E%3C/path%3E%3C/g%3E%3C/g%3E%3C/g%3E%3C/svg%3E%0A");
  }
}
