.text-input {
  font-size: 16px;
  display: block;
  width: 100%;
  height: 48px;
  min-width: 18.75rem;
  padding: 0 1rem;
  color: #152935;
  background-color: #ffffff;
  border: none;
  -ms-flex-order: 2;
  order: 2;
  border: 1px solid #515761;
  border-radius: 3px;
  transition: all 0.2s;
}

.text-input:focus, .text-input:hover {
  outline: none;
  box-shadow: 0 0 0 1px #4378A4;
  border: 1px solid #4378A4;
}

.label--disabled, .form__helper-text--disabled {
  opacity: 0.5;
}

.text-input:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.text-input[data-invalid], .text-input[data-invalid]:focus {
  box-shadow: 0 0 0 1px #D8171B, 0 0 5px 0 rgba(230,92,92,0.30);
  border: 1px solid #D8171B;
}
.text-input[data-invalid]:focus {
  box-shadow: 0 0 0 1px #D8171B, 0 0 0 2px #fff, 0 0 0 4px #D8171B, 0 0 5px 0 rgba(230,92,92,0.30);
}

.text-input~.form-requirement {
  order: 3;
  color: #e0182d;
  font-weight: 400;
  margin-top: 8px;
}


/* Text Area */
.text-area {
  font-size: 16px;
  width: 100%;
  min-width: 10rem;
  padding: 1rem;
  color: #152935;
  background-color: #fff;
  border: none;
  -ms-flex-order: 2;
  order: 2;
  resize: vertical;
  border: 1px solid #515761;
  border-radius: 3px;
  transition: all 0.2s;
}
.text-area:focus, .text-area:hover {
  outline: none;
  box-shadow: 0 0 0 1px #4378A4;
  border: 1px solid #4378A4;
}
.text-area ~ .label {
  -ms-flex-order: 1;
  order: 1;
}
.text-area[data-invalid]:focus + .label {
  color: #e0182d;
}
.text-area[data-invalid], .text-area[data-invalid]:focus {
  box-shadow: 0 0 0 1px #D8171B, 0 0 5px 0 rgba(230,92,92,0.30);
  border: 1px solid #D8171B;
}
.text-area[data-invalid]:focus {
  box-shadow: 0 0 0 1px #D8171B, 0 0 0 2px #fff, 0 0 0 4px #D8171B, 0 0 5px 0 rgba(230,92,92,0.30);
}
.text-area ~ .form-requirement {
  -ms-flex-order: 3;
  order: 3;
  color: #e0182d;
  font-weight: 400;
  margin-top: 12px;
}
