.select {
  position: relative;
  display: flex;
  flex-direction: column;
}

.select-input {
  font-size: 16px;
  height: 48px;
  appearance: none;
  display: block;
  width: 100%;
  padding: 0 16px;
  color: #152935;
  border: none;
  order: 2;
  cursor: pointer;
  background: #FFFFFF;
  border: 1px solid #515761;
  border-radius: 3px;
  transition: all 0.2s;
}

.select-option[disabled] {
  opacity: 0.5;
  cursor: not-allowed;
}

.select-input:focus, .select-input:hover {
  outline: none;
  box-shadow: 0 0 0 1px #4378A4;
  border: 1px solid #4378A4;
}

.select-input:focus ~ .label {
  color: #4378A4;
}

.select__arrow {
  fill: #515761;
  position: absolute;
  right:16px;
  bottom: 21px;
  width: 0.625rem;
  height: 0.3125rem;
  pointer-events: none;
}

[data-invalid] ~ .select__arrow {
  top: 55px;
}

.select-input ~ .label {
  -ms-flex-order: 1;
  order: 1;
}

.form-requirement {
  font-size: 16px;
  margin: 8px 0 0;
  max-height: 0;
  overflow: hidden;
  font-weight: 600;
  line-height: 1.5;
  display: none;
}

.select-input[data-invalid], .select-input[data-invalid]:focus {
  box-shadow: 0 0 0 1px #D8171B, 0 0 5px 0 rgba(230,92,92,0.30);
  border: 1px solid #D8171B;
}

.select-input[data-invalid]:focus {
  box-shadow: 0 0 0 1px #D8171B, 0 0 0 2px #fff, 0 0 0 4px #D8171B, 0 0 5px 0 rgba(230,92,92,0.30);
}

.select-input[data-invalid]:focus ~ .label {
  color: #e0182d;
}

input[data-invalid] ~ .form-requirement, textarea[data-invalid] ~ .form-requirement, select[data-invalid] ~ .form-requirement, .list-box[data-invalid] ~ .form-requirement {
  max-height: 12.5rem;
  display: block;
}

.select-input ~ .form-requirement {
  -ms-flex-order: 3;
  order: 3;
  color: #e0182d;
  font-weight: 400;
  margin-top: 8px;
}
