.search {
  display: flex;
  align-items: center;
  position: relative;
  width: 100%;
}

.search-input {
  font-size: 16px;
  font-weight: 400;
  line-height: 1.375rem;
  letter-spacing: 0px;
  color: rgb(23, 23, 23);
  text-overflow: ellipsis;
  width: 100%;
  order: 1;
  padding: 0px 2.5rem;
  transition: background-color 0.1s ease 0s, outline 0.1s ease 0s;
  height: 48px;
  color: #152935;
  background-color: #ffffff;
  border: none;
  border: 1px solid #515761;
  border-radius: 3px;
  transition: all 0.2s;
}

.search .label {
  position: absolute;
  width: 1px;
  height: 1px;
  clip: rect(0px, 0px, 0px, 0px);
  visibility: visible;
  white-space: nowrap;
  padding: 0px;
  margin: -1px;
  overflow: hidden;
  border: 0;
}

.search-magnifier {
  left: 0.75rem;
  z-index: 2;
  position: absolute;
  height: 1rem;
  width: 1rem;
  top: 50%;
  transform: translateY(-50%);
  fill: rgb(86, 86, 86);
}

.search-input:focus, .search-input:hover {
  outline: none;
  box-shadow: 0 0 0 1px #4378A4;
  border: 1px solid #4378A4;
}
